<template>
  <div class="comment-container">
    <van-list v-model="loading"
              :finished="finished"
              finished-text="没有更多了"
              :error='error'
              :immediate-check='false'
              error-text="获取数据失败，请点击重试"
              @load="onLoad">
      <!-- <van-cell v-for="(item,index) in list"
                :key="index"
                :title="item.content" /> -->
      <comment-item v-for="(item,index) in list"
                    :key="index"
                    :comment='item'
                    @reply-click="$emit('reply-click',$event)"></comment-item>
    </van-list>
  </div>
</template>

<script>
import { getCommentList } from '@/api/comment'
import commentItem from './comment-item'
export default {
  props: {
    source: [String, Number, Object],
    list: {
      type: Array,
      // 如果父组件没有传值，评论默认为空
      default: function () {
        return []
      }
    },
    type: {
      type: String,
      default: 'a'
    }
  },
  components: {
    commentItem
  },
  data () {
    return {
      loading: false,
      finished: false,
      offset: null, // 用来获取下一页的评论
      error: false
    }
  },
  methods: {
    async onLoad () {
      try {
        const dataObj = {
          type: this.type, // 评论类型，a-对文章(article)的评论，c-对评论(comment)的回复
          source: this.source.toString(), // 源id，文章id或评论id
          offset: this.offset, // 获取评论数据的偏移量，值为评论id，表示从此id的数据向后取，不传表示从第一页开始读取数据
          limit: 10 // 获取的评论数据个数，不传表示采用后端服务设定的默认每页数据量
        }
        const { data: res } = await getCommentList(dataObj)
        // console.log(res)
        this.list.push(...res.data.results)
        this.$emit('total', res.data.total_count)
        this.loading = false
        if (res.data.results.length) {
          this.offset = res.data.last_id
        } else {
          this.finished = true
        }
      } catch (err) {
        this.loading = false
        this.error = true
      }
    }
  },
  created () {
    // 自己手动调用list的滚动事件，不会自动开启loading效果
    this.loading = true
    this.onLoad()
  }
}
</script>

<style lang="scss" scoped>
.comment-container {
  padding: 0 29px;
}
</style>
