<template>
  <div>
    <van-icon name="good-job-o"
              v-if="value!==1"
              @click="changeLiked" />
    <van-icon name="good-job"
              v-else
              @click="changeLiked"
              color="orange" />
  </div>
</template>

<script>
import { addLiked, delLiked } from '@/api/article'
export default {
  props: {
    value: Number,
    art_id: [Number, String, Object]
  },
  methods: {
    async changeLiked () {
      if (this.value === 1) {
        // 已点赞，点击取消
        await delLiked(this.art_id)
        this.$toast('取消点赞成功')
        this.$emit('input', 0)
      } else {
        // 点击添加点赞
        await addLiked(this.art_id)
        this.$toast('点赞成功')
        this.$emit('input', 1)
      }
    }
  }
}
</script>

