<template>
  <div class="article-container">
    <!-- 导航 -->
    <van-nav-bar title="IT论坛"
                 left-arrow
                 @click-left="$router.push('/')" />
    <!-- 文章详情区域 -->
    <div class="art-main">
      <!-- 加载中 -->
      <van-loading color="#3296fa"
                   v-if="load"
                   vertical>加载中</van-loading>
      <div v-else-if="article.pubdate">
        <h1>{{article.title}}</h1>
        <van-cell class="user-info"
                  :border='false'
                  center>
          <van-image slot="icon"
                     class="avatar"
                     round
                     fit="cover"
                     :src="article.aut_photo" />
          <div slot="title"
               class="userbox">
            <span class="username">{{article.aut_name}}</span>
            <span class="pubtime">{{article.pubdate|relativeTime}}</span>
          </div>

          <!-- 在子组件中传递数据还要修改数据时，可以用v-model代替传递数据props和
             自定义事件（子组件的自定义事件名为input就行） -->
          <!-- 传递的数据：:is_followed='article.is_followed' -->
          <!-- 自定义事件：@chanFollowed='article.is_followed=$event' -->
          <!-- v-model会向子组件传递一个props为value -->
          <!-- v-model会监听input事件，相当于子组件的自定义事件监听@input='article.is_followed=$event'
          如果要修改v-model默认传递的vaule和input事件，可以在子组件中定义model对象来
          修改名字如，model:{props:is_followed(把value改为is_followed),event:change(把input事件改为change)}
          -->
          <follow-user v-model="article.is_followed"
                       :aut_id='article.aut_id'></follow-user>
          <!-- <van-button icon="plus"
                      v-if="!article.is_followed"
                      type="info"
                      class="btn"
                      :loading='followedload'
                      @click="addFollow"
                      round>关注</van-button>
          <van-button v-else
                      class="btn-add"
                      @click="delFollow"
                      :loading='followedload'
                      round>已关注</van-button> -->
        </van-cell>
        <div class="textContainer markdown-body"
             ref="imgContainer"
             v-html="article.content"></div>
        <p>评论列表</p>
        <comment-list :source='article.art_id'
                      @total='totalComment=$event'
                      :list='commentList'
                      @reply-click='ReplyClick'></comment-list>
        <!-- 底部区域 -->
        <div class="art-bottom">
          <van-button class="say-btn"
                      @click="popUpShow=true"
                      round>写评论</van-button>
          <like-article :art_id='article.art_id'
                        v-model="article.attitude"></like-article>
          <van-icon name="comment-o"
                    :badge="totalComment" />
          <collect-art v-model="article.is_collected"
                       :art_id='article.art_id'></collect-art>
          <van-icon name="share-o" />
        </div>
        <!-- 点击写评论弹出层 -->
        <van-popup v-model="popUpShow"
                   position="bottom">
          <pub-comment :target='article.art_id'
                       @closePopUp='pubSuccess'></pub-comment>
        </van-popup>

        <!-- 评论回复弹出层 -->
        <!-- 弹出层是懒加载，只会在第一次打开时渲染里面的内容，后面再操作都是切换内容的显示与隐藏 -->
        <van-popup v-model="replyShow"
                   position="bottom"
                   :style="{ height: '80%' }">
          <reply-comment v-if="replyShow"
                         :comment='currentComment'
                         @close='replyShow=false'></reply-comment>
        </van-popup>
      </div>
      <!-- 发生错误区域 -->
      <!-- 文章不存在 -->
      <div class="error-wrap"
           v-else-if="errStatus===404">
        <van-icon name="failure"></van-icon>
        <div>内容不存在</div>
      </div>
      <!-- 加载失败 -->
      <div class="error-wrap"
           v-else>
        <van-icon name="failure"></van-icon>
        <div>内容加载失败</div>
        <van-button @click="loadArt">点击重试</van-button>
      </div>
    </div>
  </div>
</template>

<script>
import { getArtById } from '@/api/article'
import './github-markdown.css'
import { ImagePreview } from 'vant'
import followUser from '@/components/follow-user'
import collectArt from '@/components/collection-art'
import likeArticle from '@/components/likeArticle'
import commentList from './components/comment'
import pubComment from './components/pub-comment'
import replyComment from './components/reply-comment'
export default {
  props: {
    articleid: [String, Number, Object]
  },
  // 给所有后代组件提供的数据
  provide: function () {
    return {
      isArtId: this.articleid
    }
  },
  data () {
    return {
      article: {},
      load: true,
      errStatus: 0,
      totalComment: 0,
      popUpShow: false,
      commentList: [], // 评论列表
      replyShow: false,
      currentComment: {} // 当前点击回复的评论项
    }
  },
  components: {
    followUser,
    collectArt,
    likeArticle,
    commentList,
    pubComment,
    replyComment
  },
  methods: {
    async loadArt () {
      this.load = true
      try {
        const { data: res } = await getArtById(this.articleid)
        // console.log(res)
        // if (Math.random() > 0.5) {
        //   JSON.parse('qwea')
        // }
        this.article = res.data
        // 点击图片预览
        setTimeout(() => {
          this.clickImg()
        }, 0)
        this.load = false
      } catch (err) {
        this.load = false
        if (err.response && err.response.status === 404) {
          this.errStatus = 404
        }
      }
    },
    clickImg () {
      const imgcontainer = this.$refs.imgContainer
      const imgs = imgcontainer.querySelectorAll('img')
      const images = []
      imgs.forEach((item, i) => {
        images.push(item.src)
        item.onclick = () => {
          ImagePreview({
            // 该文章中的所有图片的数组
            images,
            // 点击的位置
            startPosition: i
          })
        }
      })
    },
    // 发布成功
    pubSuccess (data) {
      this.popUpShow = false // 点击发布关闭弹出层
      this.totalComment++
      this.commentList.unshift(data.new_obj) // 把内容添加到评论列表
    },
    // 点击评论中的回复
    ReplyClick (comment) {
      this.currentComment = comment
      this.replyShow = true
    }
  },
  created () {
    this.loadArt()
  }
}
</script>

<style lang="scss" scoped>
.article-container {
  ::v-deep .van-nav-bar {
    background-color: #3296fa;
    .van-nav-bar__title,
    .van-icon-arrow-left {
      color: #fff;
    }
  }
  .art-main {
    position: fixed;
    left: 0;
    right: 0;
    top: 92px;
    bottom: 88px;
    overflow-y: scroll;
    background-color: #fff;
    .van-loading {
      height: 400px;
      margin-top: 200px;
    }
    p {
      padding-left: 29px;
    }
    h1 {
      font-size: 40px;
      color: #3a3a3a;
      margin-left: 29px;
      margin-right: 20px;
      font-weight: bold;
    }
    .textContainer {
      padding: 0 40px;
      margin-bottom: 300px;
    }
    ::v-deep .user-info {
      .van-cell__title {
        display: flex;
        .userbox {
          height: 70px;
          display: flex;
          flex-direction: column;
          .username {
            font-size: 26px;
            color: #333;
            font-weight: 550;
            line-height: unset;
          }
          .pubtime {
            font-size: 24px;
            color: #b7b7b7;
            line-height: 0;
            margin-top: 8px;
          }
        }
      }
      .btn {
        width: 170px;
        height: 58px;
        .van-loading {
          height: unset;
          margin-top: unset;
        }
      }
      .btn-add {
        width: 170px;
        height: 58px;
        background-color: #e0dfe1;
        .van-loading {
          height: unset;
          margin-top: unset;
        }
      }
    }
    .avatar {
      width: 70px;
      height: 70px;
      margin-right: 9px;
    }
  }
  .error-wrap {
    padding: 200px 32px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    .van-icon {
      font-size: 100px;
    }
    .van-button {
      margin-top: 20px;
    }
  }
  .art-bottom {
    position: fixed;
    bottom: 0;
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    height: 88px;
    border-top: 1px solid #d8d8d8;
    background-color: #fff;
    z-index: 2;
    .say-btn {
      width: 281px;
      height: 45px;
      font-size: 30px;
      color: #7a7a7a;
    }
  }
}
</style>
