<template>
  <van-button icon="plus"
              v-if="!value"
              type="info"
              class="btn"
              :loading='followedload'
              @click="addFollow"
              round>关注</van-button>
  <van-button v-else
              class="btn-add"
              @click="delFollow"
              :loading='followedload'
              round>已关注</van-button>
</template>

<script>
import { addFollowed, delFollowed } from '@/api/followed'
export default {
  props: {
    // is_followed: Boolean,
    value: Boolean,
    aut_id: [Number, String, Object]
  },
  data () {
    return {
      followedload: false
    }
  },
  methods: {
    // 添加关注
    async addFollow () {
      this.followedload = true
      try {
        await addFollowed(this.aut_id)
        this.$toast('关注用户成功')
        this.$emit('input', true)
      } catch (err) {
        if (err.response && err.response.status === 400) {
          this.$toast('不能关注自己')
        }
      }
      this.followedload = false
    },
    // 取消关注
    async delFollow () {
      this.followedload = true
      try {
        await delFollowed(this.aut_id)
        this.$toast('取消关注成功')
        this.$emit('input', false)
      } catch (err) {
        this.$toast('操作失败,请稍后重试')
      }
      this.followedload = false
    }
  }
}
</script>

