import request from '@/utils/request'

// 关注用户
export const addFollowed = (id) => {
  return request({
    method: 'post',
    url: '/app/v1_0/user/followings',
    data: {
      target: id
    }
  })
}

// 取消关注
export const delFollowed = (target) => {
  return request({
    method: 'delete',
    url: `/app/v1_0/user/followings/${target}`
  })
}
