<template>
  <div class="reply-container">
    <van-nav-bar class="nav-title-text">
      <div slot="title"
           class="titleText">{{comment.reply_count}}条回复</div>
      <van-icon slot="left"
                name="cross"
                color="#222"
                @click="$emit('close')" />
    </van-nav-bar>
    <div class="mian-comment">
      <comment-item :comment='comment'></comment-item>
      <van-cell title="全部回复"></van-cell>
      <!-- 评论回复列表 -->
      <comment-list type='c'
                    :list='replyCommentList'
                    :source='comment.com_id'></comment-list>
      <!-- 实验 -->

      <!-- 实验 -->
    </div>
    <!-- 写评论按钮 -->
    <van-button class="btn-footer"
                @click="commentShow=true"
                round
                block>写回复</van-button>
    <van-popup v-model="commentShow"
               position="bottom">
      <pub-comment :target='comment.com_id'
                   @closePopUp='replySuccess'></pub-comment>
    </van-popup>
  </div>
</template>

<script>
import commentItem from './comment-item'
import commentList from './comment'
import pubComment from './pub-comment'
export default {
  props: {
    comment: Object
  },
  data () {
    return {
      commentShow: false,
      replyCommentList: []
    }
  },
  components: {
    commentItem,
    commentList,
    pubComment
  },
  methods: {
    replySuccess (data) {
      this.comment.reply_count++
      this.commentShow = false
      this.replyCommentList.unshift(data.new_obj)
    }
  }
}
</script>

<style lang="scss" scoped>
.reply-container {
  .mian-comment {
    position: absolute;
    top: 92px;
    left: 29px;
    right: 29px;
    bottom: 88px;
    overflow-y: auto;
  }
  .btn-footer {
    position: fixed;
    bottom: 0;
    left: 25px;
    width: 700px;
  }
  ::v-deep .nav-title-text {
    background-color: #fff !important;
    .titleText {
      color: #222;
    }
  }
  .commentBox {
    padding-left: 29px;
    .van-image {
      width: 75px;
      height: 75px;
      margin-right: 25px;
    }
    ::v-deep .rightBox {
      height: 50px;
      .van-icon {
        position: relative;
        top: 6px;
        font-size: 32px;
        margin-right: 6px;
      }
      &.colorLike {
        color: red;
      }
      span {
        font-size: 24px;
      }
    }
    .commentText {
      margin-top: 30px;
      font-size: 32px;
      color: #222;
      .time-btn {
        margin-top: 40px;
        font-size: 26px;
        .van-button {
          width: 124px;
          height: 46px;
          background-color: #f4f5f6;
          margin-left: 26px;
          font-size: 26px;
          padding: 0 2px;
        }
      }
    }
  }
}
</style>
