<template>
  <div>
    <van-icon name="star"
              color="orange"
              v-if="is_collected"
              @click="changeCollected" />
    <van-icon name="star-o"
              v-else
              @click="changeCollected" />
  </div>
</template>

<script>
import { addCollected, delCollected } from '@/api/article'
export default {
  model: {
    prop: 'is_collected',
    event: 'changeCollected'
  },
  props: {
    is_collected: Boolean,
    art_id: [Number, String, Object]
  },
  methods: {
    async changeCollected () {
      if (this.is_collected) {
        // 取消收藏
        await delCollected(this.art_id)
        this.$toast('取消收藏成功')
      } else {
        // 添加收藏
        await addCollected(this.art_id)
        this.$toast('收藏文章成功')
      }
      this.$emit('changeCollected', !this.is_collected)
    }
  }
}
</script>

