<template>
  <div>
    <van-cell>
      <van-field slot="title"
                 v-model.trim="message"
                 rows="2"
                 autosize
                 type="textarea"
                 maxlength="50"
                 placeholder="请输入评论"
                 show-word-limit />
      <van-button slot="right-icon"
                  plain
                  @click="pubComment"
                  :disabled='!message.length'
                  type="info">发送</van-button>
    </van-cell>
  </div>
</template>

<script>
import { addComment } from '@/api/comment'
// import jsonBigint from 'json-bigint'
export default {
  // 从祖辈组件拿传过来的数据
  inject: {
    isArtId: {
      type: [String, Number, Object],
      default: null
    }
  },
  props: {
    target: [String, Number, Object]
  },
  data () {
    return {
      message: ''
    }
  },
  methods: {
    async pubComment () {
      this.$toast.loading({
        message: '发送中...',
        forbidClick: true, // 禁止背景点击
        duration: 0// 展示时长(ms)，值为 0 时，toast 不会消失
      })
      try {
        const dataObj = {
          target: this.target.toString(), // 评论的目标id（评论文章即为文章id，对评论进行回复则为评论id）
          content: this.message, // 评论内容
          art_id: this.isArtId ? this.isArtId.toString() : this.isArtId // 文章id，对评论内容发表回复时，需要传递此参数，表明所属文章id。对文章进行评论，不要传此参数
        }
        const { data: res } = await addComment(dataObj)
        // console.log(res)
        this.$toast.success('发送评论成功')
        this.message = ''
        // 发布成功，通知父组件关闭弹层，并把内容添加到评论列表
        this.$emit('closePopUp', res.data)
      } catch (err) {
        this.$toast('发送失败，请稍后重试')
      }
      this.loading = false
    }
  }
  // computed: {
  //   // 文章id
  //   artid () {
  //     try { return jsonBigint.stringify(this.article.art_id) } catch (err) {
  //       return this.article.art_id
  //     }
  //   },
  //   // 评论id
  //   autid () {
  //     try { return jsonBigint.stringify(this.article.aut_id) } catch (err) {
  //       return this.article.aut_id
  //     }
  //   }
  // }
}
</script>

<style lang="scss" scoped>
.van-button {
  border: 0;
  margin-top: 25px;
}
</style>
