<template>
  <div>
    <van-cell class="commentBox">
      <van-image slot="icon"
                 round
                 fit="cover"
                 :src="comment.aut_photo" />
      <div slot="title"
           class="author">{{comment.aut_name}}</div>
      <div slot="right-icon"
           class="rightBox"
           :class="{colorLike:comment.is_liking}"
           @click="addLiked">
        <van-icon :name="comment.is_liking?'good-job':'good-job-o'" />
        <span>{{comment.like_count>0?comment.like_count:'赞'}}</span>
      </div>
      <div slot="label"
           class="commentText">
        <div>{{comment.content}}</div>
        <div class="time-btn"
             @click="showReply">
          <span>{{comment.pubdate|relativeTime}}</span>
          <van-button round>{{comment.reply_count}}回复</van-button>
        </div>
      </div>
    </van-cell>
  </div>
</template>

<script>
import { addLike, delLike } from '@/api/comment'
// import jsonBigint from 'json-bigint'
export default {
  props: {
    comment: Object
  },
  data () {
    return {

    }
  },
  methods: {
    async addLiked () {
      if (this.comment.is_liking) {
        // 已点赞，点击取消点赞
        await delLike(this.comment.com_id.toString())
        this.comment.like_count -= 1
        this.$toast('取消点赞成功')
      } else {
        // 添加点赞
        await addLike(this.comment.com_id.toString())
        this.comment.like_count += 1
        this.$toast('点赞成功')
      }
      this.comment.is_liking = !this.comment.is_liking
    },
    showReply () {
      this.$emit('reply-click', this.comment)
    }
  }
  // computed: {
  //   comid () {
  //     try { return jsonBigint.stringify(this.comment.com_id) } catch (err) {
  //       return this.comment.com_id
  //     }
  //   }
  // }
}
</script>

<style lang="scss" scoped>
.commentBox {
  padding-left: 0;
  .van-image {
    width: 75px;
    height: 75px;
    margin-right: 25px;
  }
  ::v-deep .rightBox {
    height: 50px;
    .van-icon {
      position: relative;
      top: 6px;
      font-size: 32px;
      margin-right: 6px;
    }
    &.colorLike {
      color: red;
    }
    span {
      font-size: 24px;
    }
  }
  .commentText {
    margin-top: 30px;
    font-size: 32px;
    color: #222;
    .time-btn {
      margin-top: 40px;
      font-size: 26px;
      .van-button {
        width: 124px;
        height: 46px;
        background-color: #f4f5f6;
        margin-left: 26px;
        font-size: 26px;
        padding: 0 2px;
      }
    }
  }
}
</style>
