import request from '@/utils/request'

// 请求文章列表
export const getArtList = (params) => {
  return request({
    method: 'get',
    url: '/app/v1_1/articles',
    params
  })
}

// 获取文章详情
export const getArtById = (id) => {
  return request({
    method: 'get',
    url: `/app/v1_0/articles/${id}`
  })
}

// 收藏文章
export const addCollected = (target) => {
  return request({
    method: 'post',
    url: '/app/v1_0/article/collections',
    data: {
      target
    }
  })
}

// 取消收藏文章
export const delCollected = (target) => {
  return request({
    method: 'delete',
    url: `/app/v1_0/article/collections/${target}`
  })
}

// 点赞文章
export const addLiked = (target) => {
  return request({
    method: 'post',
    url: '/app/v1_0/article/likings',
    data: {
      target
    }
  })
}

// 取消点赞文章
export const delLiked = (target) => {
  return request({
    method: 'delete',
    url: `/app/v1_0/article/likings/${target}`
  })
}
